@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0px;
  padding: 0px;
}

.navbar-toggler {
  margin-left: 85%;
}
.fonts {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
}
.particles_container {
  overflow: hidden;
  position: absolute;
  top: 0px;
  left: 0px;
  bottom: 0px;
  right: 0px;
  z-index: 0;
}
.particles_container canvas {
  transition: 0.2s all ease-in-out;
  z-index: 0;
  height: 100%;
  width: 100%;
}
/* Firefox */

body::-webkit-scrollbar {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Montserrat, Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 16px;
  display: none;

  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none;
  max-height: 100%;
  overflow: hidden;
}

.btext {
  color: #f9f3f4;
  position: absolute;
  text-align: center;
  top: 35%;
  right: 21%;
  width: 100%;
  line-height: 0.4em;
}

h1 {
  font-size: 50px;
}

.btn {
  color: #fff;
  text-decoration: none;
  border: #ccc 1px solid;
  padding: 10px 15px;
  border-radius: 8px;
  /* line-height: 4em; */
}

.btn:hover {
  color: #b19295;
  border: #fff 1px solid;
}

#particles-js {
  background-image: url(/static/media/test_bg.69f9e0ca.png);
  height: 100vh;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
}

.contain {
  position: relative;
  text-align: center;
  color: white;
}

.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.blg {
  margin: 2%;
  margin-bottom: 8%;
}

a:link {
  text-decoration: none;
}

.card:hover {
  /* transform: scale(1.03); */
  box-shadow: 15px black;
}

.team {
  margin: 3%;
}

