.hide {
  display: none;
}

.map {
  padding: 0px;
  margin: 6%;
}
.container1 {
  position: relative;
  text-align: center;
  color: white;
  padding: 0px;
  margin: 0px;
  margin-right: 0%;
  background-color: rgb(30, 61, 85) !important;
  width: 100% !important;
}

.bottom-left {
  position: absolute;
  bottom: 10%;
  left: 5%;
}

iframe {
  height: 22rem !important;
  width: 30rem;
}
.clg-img {
  height: 43rem !important;
  width: 100% !important;
  -webkit-filter: opacity(0.2);
  filter: opacity(0.5);
  /* filter: alpha(opacity=.1); */
  /* filter: blue-wash(90%); */
}

.card-width {
  max-width: 50%;
}

.card-row {
  width: 80%;
  margin: auto;
  padding: auto;
}

